.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-header {
  background-color: #f7f7f7;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: center; */
  // font-size: calc(10px + 2vmin);
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  box-sizing: boder-box;
}
.container{
  margin:0 auto;
  max-width: 1440px;
  width: calc(100% - 10px);
  padding: 5px;
}

.wrapper{
  display:flex;
  width:100%;
  flex-direction: column;
  .left-sidebar{
    display:flex;
    width: 20%;
    margin-right:30px;
    .filter-con{
      background: white;
      border-radius: 5px;
      border: 1px solid grey;
      padding: 10px;
      width:100%;
      h2{ margin:0;}
      .inner-filterbox{
        h4{
          text-align: center;
          position: relative;
          &::after {
            content: '';
            border-bottom: 1px solid grey;
            height: 2px;
            position: absolute;
            width: 75%;
            bottom: -2px;
            left: 24px;
          }
        
        }
        .filterholder {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          button {
            width: 48%;
            margin-bottom: 5px;
            border:none;
            background-color:#8eda8e;
          }
        }
      }
    }
  }
  .main-area {
    display:flex;
    width:100%;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-top: -10px;
    flex-direction: column;
    .boxWrapper{
      width: 100%;
      background: white;
      border-radius: 5px;
      border: 1px solid grey;
      padding: 10px;
      max-width: 300px;
      margin: 10px auto;
      .imgCon{
        background:#f3f3f3;
        padding: 5px;
        margin-bottom: 10px;
        .img-responsive{
          width:100%;
        }
      }
      .title{
        strong {
          color: #2222a2;
        }
      }
      .boxContent{
        padding:0;
        margin:0;
        .itemlist {
          list-style: none;
          display: flex;
          justify-content: space-around;
          span{
            display: flex;
            width: 100%;
            font-size: 16px;
          }
        }
      }
    }
  }
}


@media only screen and (min-width: 1441px){
  .wrapper{
    flex-direction: row;
    .main-area {
      flex-direction: row;
      .boxWrapper{
        width: 20%;
        flex: 0 auto;
        margin-left: 10px;
        margin-top: 10px;
      }
    }
  }
  
}
@media only screen and (min-width: 1025px) and (max-width: 1440px){
  .wrapper{
    flex-direction: row;
    .main-area {
      flex-direction: row;
      .boxWrapper{
        width: 20%;
        flex: 0 auto;
        margin-left: 10px;
        margin-top: 10px;
      }
    }
  }
  
}
@media only screen and (min-width: 701px) and (max-width: 1024px){
  .wrapper{
    flex-direction: row;
    .left-sidebar{
      width: 33%;
    }
    .main-area {
      flex-direction: row;
      .boxWrapper{
        flex: 1 0 calc(33% - 10px);
        width: 50%;
        margin-left: 10px;
        margin-top: 10px;
      }
    }
  }
}


